import arrays from './mockArrays';
import concept from './mockConcept';
import city from './mockCity';
import request from 'superagent';

const url = 'http://rielt.grechka.digital';

export const fetchArrays = async () => {
	const {body} = await request.get(
    url+'/api/v1/all-arrays'
	);
	return body.data;

	// return new Promise(resolve => {
	// 	resolve(arrays);
	// });
}

export const fetchConcept = async () => {
	const {body} = await request.get(
    url+'/api/v1/all-concepts'
	);
	return body.data;

	// return new Promise(resolve => {
	// 	resolve(concept);
	// });
}

export const fetchCity = async () => {
	const {body} = await request.get(
    url+'/api/v1/all-cities'
	);
	return body.data;

	// return new Promise(resolve => {
	// 	resolve(city);
	// });
}


