
import React from 'react';
import { Field, reduxForm } from 'redux-form';
import classnames from 'classnames';

const SimpleForm = props => {
  const { handleSubmit } = props;

  const textInput = ({ input, meta: { touched, error }, ...custom }) => {
    const hasError = touched && error !== undefined;
    const inputClassName = classnames('form-field popup-wrap__input', { 'error': hasError });
    return (
      <div className={inputClassName}>
        <input type="text" className="form-field__input" placeholder="Имя" />
        { hasError && <span className="form-field__msg">{error}</span> }
      </div>
    );
  }

  return (
    <form
      className="popup-wrap__form"
      onSubmit={handleSubmit}
    >
      <div className="form-field popup-wrap__input ">
        <Field
          name="firstName"
          component="textInput"
          type="text"
          placeholder="First Name"
        />
      </div>
      <div className="form-field popup-wrap__input">
        <Field
          name="lastName"
          component="input"
          type="text"
          placeholder="Last Name"
        />
      </div>
      <button type="submit" className="btn btn_color_ac popup-wrap__button">Забронировать</button>
    </form>
  );
};

export default reduxForm({
  form: 'simple', // a unique identifier for this form
})(SimpleForm);



